.title-screen {
  background-image: url(../img/bg_screen1.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50% 50%;

  &__content {
    position: relative;
    z-index: 2;
  }
  &__wrapper {
    display: flex;
    padding-top: 130px;
    padding-bottom: 230px;
    position: relative;
    @media screen and (max-width: 1420px) {
      padding-bottom: 150px;
    }
    @media screen and (max-width: 1300px) {
      padding-top: 80px;
      padding-bottom: 80px;
    }
    @media screen and (max-width: 970px) {
      display: block;
    }
    @media screen and (max-width: 720px) {
      padding-top: 50px;
      padding-bottom: 50px;
    }
    @media screen and (max-width: 450px) {
      padding-top: 15px;
    }
  }
  &__form {
    width: 410px;
    margin-left: auto;
    position: relative;
    z-index: 2;
    flex-shrink: 0;
    align-self: center;
    @media screen and (max-width: 1150px) {
      width: 360px;
    }
    @media screen and (max-width: 970px) {
      width: 100%;
    }
  }
  &__abs {
    position: absolute;
    bottom: 0;
    z-index: 1;
    right: 260px;
    @media screen and (max-width: 1420px) {
      right: 320px;
    }

    @media screen and (max-width: 1300px) {
      display: none;
    }
    &-desc {
      position: absolute;
      right: 100%;
      bottom: 100px;
      width: 200px;
      b {
        display: block;
        margin-bottom: 20px;
        color: #000000;
        font-size: 20px;
        font-weight: 500;
      }
      span {
        color: rgba(0, 0, 0, 0.51);
        font-size: 16px;
      }
    }
    &-img {
      display: block;
      @media screen and (max-width: 1420px) {
        width: 380px;
      }
    }
  }
}

.title-screen-text {
  @media screen and (max-width: 450px) {

  }
  &__desc {
    font-weight: 500;
    font-size: 20px;
    color: #000000;
    margin-bottom: 30px;
    @media screen and (max-width: 450px) {
      font-size: 15px;
      text-align: center;
      margin-bottom: 12px;
    }
  }

  &__date {
    margin-bottom: 40px;

    @media screen and (max-width: 470px) {
      display: flex;
      flex-direction: column;
      align-items: center;
    }
    @media screen and (max-width: 450px) {
      margin-bottom: 20px;
    }

    strong {
      display: inline-block;
      vertical-align: middle;
      background-color: #EAF3F9;
      border: 1px solid #FFB800;
      border-radius: 5px;
      padding: 15px 20px 13px;
      margin-right: 20px;
      font-size: 12px;
      @media screen and (max-width: 470px) {
        margin-right: 0;
        margin-bottom: 14px;
      }
      @media screen and (max-width: 450px) {
        padding: 7px 20px 6px;
        margin-bottom: 8px;
      }
    }

    span {
      font-size: 14px;
      color: rgba(0, 0, 0, 0.51);
      @media screen and (max-width: 450px) {
        text-align: center;
      }
    }
  }

  &__title {
    font-size: 36px;
    font-weight: 500;
    line-height: 1.25;
    text-transform: uppercase;
    margin-bottom: 60px;
    max-width: 700px;
    @media screen and (max-width: 1150px) {
      font-size: 40px;
      margin-bottom: 40px;
    }
    @media screen and (max-width: 550px) {
      font-size: 32px;
      br {
        display: none;
      }
    }

    @media screen and (max-width: 450px) {
      font-size: 18px;
      margin-bottom: 18px;
    }

    strong {
      font-weight: 500;
      color: #FFB800;
      text-shadow: 0 0 1em rgba(#FFB800, .5);
    }
  }

  &__list-desc {
    font-size: 18px;
    @media screen and (max-width: 450px) {
      font-size: 14px;
    }
  }
  &__list {
    list-style: none;
    margin: 0;
    padding: 0;
    font-weight: 400;
    font-size: 20px;
    color: #000000;
    margin-bottom: 75px;
    @media screen and (max-width: 720px) {
      margin-bottom: 40px;
      font-size: 16px;
    }
    @media screen and (max-width: 450px) {
      font-size: 14px;
    }

    li {
      margin-bottom: 20px;
      @media screen and (max-width: 450px) {
        margin-bottom: 4px;
      }

      &:last-child {
        margin-bottom: 0;
      }

      &::before {
        content: '';
        display: inline-block;
        vertical-align: middle;
        width: 33px;
        height: 33px;
        background-image: url(data:image/png;base64,);
        background-color: transparent;
        background-repeat: no-repeat;
        background-position: 50% 50%;
        background-size: contain;
        margin-right: 12px;
        @media screen and (max-width: 450px) {
          width: 22px;
          height: 22px;
          margin-right: 6px;
        }
      }
    }
  }

  .btn {
    @media screen and (max-width: 970px) {
      display: none;
    }
  }

}
.header {
  &__wrapper {
    padding-top: 50px;
    padding-bottom: 30px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.15);
    display: flex;
    align-items: center;
    justify-content: space-between;
    @media screen and (max-width: 1140px) {
      flex-wrap: wrap;
      padding-top: 20px;
      padding-bottom: 20px;
    }
    @media screen and (max-width: 450px) {
      padding-top: 10px;
      padding-bottom: 10px;
    }
  }

  &__logo {
    a {
      display: block;
      text-decoration: none;
      transition: opacity .35s;

      &:hover {
        opacity: .55;
      }
    }

    img {
      display: block;
      width: 50px;
    }
  }

  &__menu {
    font-size: 14px;
    @media screen and (max-width: 1140px) {
      order: 2;
      width: 100%;
      margin-top: 25px;
      display: flex;
      justify-content: center;
    }
    @media screen and (max-width: 570px) {
      position: fixed;
      width: 100%;
      height: 100vh;
      background-color: #ffffff;
      z-index: 99;
      top: 0;
      left: 0;
      margin: 0;
      padding: 70px 30px 20px;
      visibility: hidden;
      opacity: 0;
      transition: all .35s;
      overflow-y: auto;
      &_open {
        visibility: visible;
        opacity: 1;
      }
    }

    ul {
      padding: 0;
      margin: 0;
      list-style: none;
      display: flex;
      @media screen and (max-width: 890px) {
        width: 100%;
        text-align: center;
      }
      @media screen and (max-width: 570px) {
        display: block;
      }
    }

    li {
      padding: 0 50px;
      border-right: 1px solid rgba(0, 0, 0, 0.2);

      &:last-child {
        border-right: none;
        @media screen and (max-width: 570px) {
          border-bottom: 0;
        }
      }

      @media screen and (max-width: 890px) {
        padding: 0;
        flex-grow: 1;
      }
      @media screen and (max-width: 570px) {
        border-right: 0;
        border-bottom: 1px solid rgba(0, 0, 0, 0.2);
      }
    }

    a {
      text-transform: uppercase;
      color: #000000;
      text-decoration: none;
      display: block;
      font-weight: 500;
      transition: color .25s;

      &:hover {
        text-decoration: none;
        color: #FFB800;
      }

      @media screen and (max-width: 570px) {
        padding: 15px 0;
      }
    }
  }

  &__contacts {
    .btn {
      img {
        vertical-align: middle;
        margin-right: 6px;
      }
    }
    @media screen and (max-width: 570px) {
      margin-left: auto;
    }
    @media screen and (max-width: 440px) {
      .btn {
        font-size: 14px;
        padding: 10px;
      }
    }
  }

  &__burger,
  &__menu-close {
    display: none;
  }
  
  &__burger {
    @media screen and (max-width: 570px) {
      display: block;
      font-size: 0;
      border: 0;
      background-color: transparent;
      background-repeat: no-repeat;
      background-position: 50% 50%;
      background-size: contain;
      background-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAACAAAAAgCAMAAABEpIrGAAAAHlBMVEUAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAC3KG9qAAAACXRSTlMABElKVYOFhod+miWwAAAAMElEQVR42mMYJoCRmQUnYGYEKuDgxAPYgArY8SlgBVnBhNsKJkYU14yGw2g4DA8AADRoCNGch9mJAAAAAElFTkSuQmCC);
      width: 32px;
      height: 32px;
      cursor: pointer;
      margin-left: 32px;
    }
    @media screen and (max-width: 390px) {
      margin-left: 16px;
    }
  }

  &__menu-close {
    @media screen and (max-width: 570px) {
      display: block;
      position: absolute;
      top: 12px;
      right: 12px;
      width: 24px;
      height: 24px;
      background-color: transparent;
      background-repeat: no-repeat;
      background-position: 50% 50%;
      background-size: contain;
      background-image: url(data:image/png;base64,);
      font-size: 0;
      border: 0;
      cursor: pointer;
    }
  }
}